import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import moment from "moment"
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { Table } from "react-bootstrap"

import '../assets/css/style3.min.css';
import "bootstrap/dist/css/bootstrap.min.css";

import RegexHelper from "./Users/regex_helper"


const DoReservation = () => {
    const [value, onChange] = useState(new Date());
    const [date, getDate] = useState();
    const [time, setTime] = useState("09:00:00");
    const [headcount, setHeadcount] = useState();

    const regexHelper = new RegexHelper();
    const usenavigate = useNavigate();
    //     
    //  value     .
    useEffect(() => {
        (async () => {
            try {
                let parseDate = moment(value).format("YYYY-MM-DD")
                await axios.get("/doReservation/selectreservation" + parseDate).then(response => { getDate(response.data) })
            } catch (error) {
                // const errorMsg = "[" + error.response.status + "] " + error.response.statusText;
                console.log(error);
                return;
            }
        })()
    }, [value])

    //   
    const compareDate = () => {
        const nowDate = parseInt(moment(new Date()).format("YYYYMMDD"))
        const selectDate = parseInt(moment(value).format("YYYYMMDD"))

        if (!regexHelper.isSameDate(nowDate, selectDate, "  .")) {
            onChange(new Date())
            return false;
        }
        if (!regexHelper.value("#headCount", "  .")) { return false; }

        Swal.fire({
            customClass: {
                container: 'my-swal'
            },
            title: "   \n?",
            html: "<p>: " + moment(value).format("YYYY-MM-DD") + "</p><p>: " + time.substr(0, 5) + " </p><p>: " + headcount + "</p>",
            icon: 'info',
            confirmButtonText: ''
        }).then((result) => {
            (async () => {
                try {
                    await axios.post("/doReservation/reserve", {
                        reserve_date: moment(value).format("YYYY-MM-DD"),
                        reserve_time: time,
                        reserve_headcount: headcount
                    }).then(response => { getDate(response.data) });
                } catch (error) {
                    console.log(error.response)
                    if (error.response.status === 401 || error.response.status === 419) {
                        Swal.fire({
                            customClass: {
                                container: 'my-swal'
                            },
                            text: error.response.data.rtmsg,
                            icon: 'error',
                            confirmButtonText: ''
                        }).then((result) => { usenavigate("/") })
                    }else{
                        Swal.fire({
                            customClass: {
                                container: 'my-swal'
                            },
                            text: error.response.data.rtmsg,
                            icon: 'info',
                            confirmButtonText: ''
                        }).then((result) => { usenavigate("/doReservation") })
                    }
                    return;
                }
            })()
        })


    }

    const setReserveTime = (e) => {
        setTime(e.target.value)
    };

    const setReserveHeadCount = (e) => {
        setHeadcount(e.target.value)
    }

    return (
        <div className='doREservationContainer'>
            <div className='doREservationContainerItem'>
                <p className="doREservationP"> </p>
                <Calendar
                    onChange={onChange} // useState      
                    formatDay={(locale, date) => moment(date).format("DD")} // ''    
                    value={value}
                    minDetail="month" //   ''   
                    maxDetail="month" //   ''   
                    navigationLabel={null}
                    showNeighboringMonth={false} //  ,      
                    className="mx-auto w-full text-sm border-b"
                />

                <Table striped bordered hover >
                    <thead>
                        <tr>
                            <th> </th>
                            <th> </th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {date !== undefined && date.data.length !== 0 ?
                            date.data.map((item, index) => (
                                <tr key={index}>
                                    <td>{item.reserve_date}</td>
                                    <td>{item.reserve_time}</td>
                                    <td>{item.reserve_headcount}</td>
                                </tr>
                            ))
                            :
                            <tr>
                                <td colSpan={3}>  .</td>
                            </tr>}
                    </tbody>
                </Table>

                <div className="selectReservationContainer">
                    <p className='selectDateP'> </p>
                    <Table striped bordered >
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th className="headCountP"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>{moment(value).format("YYYY-MM-DD")}</td>
                                <td>
                                    <select onChange={setReserveTime}>
                                        <option value={"09:00:00"}>09 00</option>
                                        <option value={"12:00:00"}>12 00</option>
                                        <option value={"15:00:00"}>15 00</option>
                                        <option value={"17:30:00"}>17 30</option>
                                    </select>
                                </td>
                                <td>
                                    <input id="headCount" onChange={setReserveHeadCount} type="text" placeholder="0" />
                                </td>
                            </tr>

                        </tbody>
                    </Table>
                    <p>   09 00 ~ 17 30  (3    )</p>
                </div>
                <div className="button-container">
                    <button onClick={compareDate} className="ReservationSubmitBtn">
                        
                    </button>
                </div>
            </div>
        </div>
    );
};

export default DoReservation;